import React, { useEffect, useState } from "react";
import { fetchDailydata } from "../../Axios"; //fetchDailydata function comes here
import { Line, Bar } from "react-chartjs-2";
function Charts(props) {
  let { confirmed, deaths, recovered } = props.countrie;
  const [dailydata, setdailydata] = useState({});
  useEffect(() => {
    let res = async () => {
      setdailydata(await fetchDailydata()); // here i call fetchDailydata() its a function that comes in Axios.jsx
    }; 
    res();
  }, []);
  const lineChart = dailydata.length ? ( // here i use condition if length is 0 then the bellow code don't run or render
    <Line // for line bar
      data={{
        labels: dailydata.map(({ confirmed }) => confirmed),
        datasets: [
          {
            data: dailydata.map(({ confirmed }) => confirmed),
            label: "confirmed",
            borderColor: "blue",
            backgroundColor: "#1b87e5",
            fill: true,
          },
          {
            data: dailydata.map(({ deaths }) => deaths),
            label: "deaths",
            borderColor: "red",
            backgroundColor: "rgba(255,0,0,0.5",
            fill: true,
          },
        ],
      }}
    />
  ) : null;
  // for barchart
  const barchart = (
    <Bar //here i use bar
      data={{
        labels: ["confirmed", "Recovered", "Deaths"],
        datasets: [
          {
            label: "People",
            backgroundColor: ["blue", "green", "red"],
            data: [confirmed, recovered, deaths],
          },
        ],
      }}
      options={{
        legend: { display: false },
        title: { display: true, text: `Current state in ${props.city}` },
        animation: {
          duration: 2000,
          easing: "easeInOutElastic",
        },
      }}
    />
  );
  return (
    <div style={{ width: "80%", marginLeft: "auto", marginRight: "auto" }}>
      {/* here i use a condition if props.city === globally thn linechart show on web browser else barchart show on web browser */}
      {props.city === "Globally" ? lineChart : barchart}

      <div className="mb-3" style={{ fontFamily: "monospace" }}>
        <hr />
        <strong className="mt-3 mb-2">
          Copyright &copy; 2021 || created by Mr.Guddu ðŸ˜Ž
        </strong>
        <p>
          Email id &#9993;: <a href="#email">jnana.ranjan408@gmail.com</a>{" "}
          <br /> WhatsApp No.&#9990;: <a href="#phone">+91 8114994487</a> <br />
          <a href="https://www.linkedin.com/in/mr-gudu-%F0%9F%87%AE%F0%9F%87%B3-35121a1b1/">
            Linkedin ID{" "}
          </a>
        </p>
        <em style={{ borderBottom: "2px solid blue", marginBottom: "20px" }}>
          Thank you :) ðŸ’š
        </em>
      </div>
    </div>
  );
}

export default Charts;
